@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap");
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 100%;
}

body {
  font-family: "Inter", sans-serif;
}

img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  display: block;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  all: unset;
  cursor: pointer;
}

button {
  font-size: medium;
  font-weight: 500;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-radius: 0.5rem;
}

a {
  color: #FFFFFF;
}
a:hover {
  color: #FFE492;
}

.flex-between-center {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.container {
  max-width: 90rem;
  margin-inline: auto;
  padding-inline: 2rem;
}

.navbar {
  padding-top: 1rem;
  padding-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navbar__links {
  display: none;
  color: #4F9CF9;
}
@media (min-width: 79em) {
  .navbar__links {
    display: block;
  }
  .navbar__links li {
    display: inline-block;
    position: relative;
    margin-right: 2rem;
  }
}
.navbar__links .navbar__link {
  position: relative;
}
.navbar__links .navbar__link:after {
  content: "";
  display: inline-block;
  background: url("../icons/down-arrow.svg") no-repeat center;
  background-size: contain;
  width: 1.5rem;
  height: 1.5rem;
  margin-left: 0.15rem;
  position: relative;
}
.navbar__dropdown {
  position: absolute;
  display: none;
  z-index: 1;
  background-color: #4F9CF9;
}
.navbar__dropdown--show {
  display: block;
}
.navbar__dropdown a {
  padding: 0.5rem 0.5rem;
  display: block;
}
.navbar__buttons {
  display: flex;
  align-items: center;
}
.navbar__buttons button {
  margin-inline: 0.625rem;
}
.navbar__cta {
  display: none;
}
@media (min-width: 48.0625em) {
  .navbar__cta {
    display: block;
  }
}
@media (min-width: 79em) {
  .navbar__hamburger-menu--disable {
    display: none;
  }
}

.btn-primary {
  background-color: #FFE492;
  padding: 1rem 2.5rem;
  color: #043873;
}

.btn-secondary {
  background-color: #4F9CF9;
  color: #FFFFFF;
  padding: 1rem 1.5rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.btn-secondary:after {
  content: "→";
  margin-left: 0.5rem;
  font-size: 1rem;
  color: #FFFFFF;
}